import React, {FC, memo} from 'react';
import { Link, useHistory } from 'react-router-dom';
import {FiUser} from "react-icons/fi";
import {FiLock} from "react-icons/fi";
import {FaSpinner} from "react-icons/fa";
import { useState } from 'react';

interface Props{};

const Login: FC<Props> = (props) => {

    // const [username, setUsername] = useState("");
    // const [password, setPassword] = useState("");
    const [data, setData] = useState({username: "", password: ""});
    const [touched, setTouched] = useState({username: false, password: false});
    const [submitting, setSubmitting] = useState(false);

    const history = useHistory();

    // the name of element whose value will be changed will be stored in the array 
    // event.target.name will be either username or password and it will be used as key
    const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        setData({...data, [event.target.name]: event.target.value});
    };

    // if cursor goes in and comes out then blur true i.e. element is touched
    const handleBlur = (event: React.FocusEvent<HTMLInputElement>) => {
        setTouched({...touched, [event.target.name]: true});
    };

    let usernameError = "";
    let passwordError = "";

    if(!data.username){
        usernameError = "Username is required";
    }
    else if(data.username.includes(" ") || data.username.includes(",") || data.username.includes("%")){
        usernameError = "Do not enter space or comma or percentage"; 
    }

    if(!data.password){
        passwordError = "Password is required";
    }
    else if(data.password.length < 6){
        passwordError = "Password should be of atleast 6 characters"
    }
    else if(data.password.includes(" ")){
        passwordError = "Do not enter space"; 
    }
    return (
        <>
            <div className="flex flex-col items-center py-6 mx-auto">
                <div className="w-full">
                    <h1 className="text-4xl font-mediun mb-2 text-gray-700">Log In to 
                        <a className="text-blue-600 font-medium" target="_blank" href="https://designreset.com/cork/ltr/demo4/index.html"> CORK</a> </h1>
                        <p className="mb-14 text-sm font-medium text-gray-700">New Here?   
                        <Link to="/signup">
                            <span className="text-blue-700 border-b border-blue-700 font-mediun"> Create an account</span>    
                        </Link>
                        </p>
                </div>
                    {/* <br></br> 
                    <Link to="/dashboard">
                        <span className="text-blue-600">Go to Dashboard</span>    
                    </Link> */}
                    <form action="" className="text-left"
                        onSubmit={(event) => {
                            event.preventDefault();
                            if(usernameError || passwordError){
                                console.log("Form submission failed");
                                return;
                            }
                            setSubmitting(true);
                            console.log("Login started with data: ", data);

                            setTimeout(() => {
                                console.log("Login Successful");
                                history.push("/dashboard");
                            }, 3000);
                        }}>
                        <div className="w-96">
                            <div className="flex items-center border-b pb-3.5 mt-3 hover:border-blue-700">
                                <FiUser className="text-blue-700 text-2xl"></FiUser>
                                <input required type="text" id="username" name="username" value={data.username} 
                                // onChange={(event) => {setData({...data, username:event.target.value});}} 
                                onChange={handleChange} onBlur={handleBlur} placeholder="Username"
                                className="pl-2 text-md tracking-wide outline-none"></input>
                            </div>
                            {touched.username && <div className="text-red-600 text-sm">{usernameError}</div>}

                            <div className="flex items-center border-b pb-3.5 mt-8 hover:border-blue-700">
                                <FiLock className="text-blue-700 text-2xl"></FiLock>
                                <input required type="password" id="password" name="password" value={data.password} 
                                // onChange={(event) => {setData({...data, password:event.target.value})}} 
                                onChange={handleChange} onBlur={handleBlur} placeholder="Password"
                                className="pl-2 text-md tracking-wide outline-none"></input>
                            </div>
                            {touched.password && <div className="text-red-600 text-sm">{passwordError}</div>}

                            <div className="flex justify-between mt-8">
                                <div className="text-left flex items-center">
                                    <p className="inline-block text-sm tracking-wide font-medium text-gray-700">Show Password</p>
                                    <div className="w-8 h-4 ml-2 flex items-center bg-gray-200 rounded-full py-1 pl-1 cursor-pointer">
                                        <div className="bg-blue-600 w-3 h-3 rounded-full shadow-md"></div>
                                    </div>
                                </div>
                                <div className="pr-2 flex items-center">
                                    {/* <Link to="/dashboard"></Link> */}
                                    <button type="submit" className="bg-blue-700 text-white rounded py-1 px-3.5 shadow-xl">Log In</button>
                                    {submitting && <FaSpinner className="ml-2 text-blue-700 text-xl animate-spin"></FaSpinner>}
                                </div>
                            </div>
                            <div className="mt-12 text-center mb-4">
                                    <label htmlFor="checkbox" className="pl-4 text-gray-400 text-md cursor-pointer">
                                        <input type="checkbox" id="checkbox" className="cursor-pointer mr-3"></input>
                                        Keep me logged in
                                    </label>
                            </div>
                            <div className="text-center text-blue-600 font-medium">
                                <Link to="/forgotPassword">
                                    Forgot Password ?
                                </Link>
                            </div>
                        </div>
                    </form>
                    <p className="mt-auto text-gray-900 font-sm">
                    Â© 2020 All Rights Reserved. 
                    <a className="text-blue-600 font-medium" target="_blank" href="https://designreset.com/cork/ltr/demo4/index.html"> CORK </a>
                    is a product of <br/> Designreset. 
                    <a className="text-blue-600 font-medium" href="javascript:void(0);"> Cookie Preferences,</a>
                    <a className="text-blue-600 font-medium" href="javascript:void(0);"> Privacy, and</a>
                    <a className="text-blue-600 font-medium" href="javascript:void(0);"> Terms.</a>
                    </p>
            </div>
        </>
    );
};

Login.defaultProps = {};

export default memo(Login); 